import React from 'react'
import { Link } from 'gatsby'
import menuMobile from './../../../content/assets/menu-mobile.svg'
import closeMobile from './../../../content/assets/close.svg'

import './index.scss'

export const NavigationBar = (props) => {
  return (
    <div className='navigation'>
      <header className='top'>
        <div className='mobile-header'>
          <Link to='/'>
            <div className='logo' />
          </Link>
          {props.isMobileOpen
            ?
            <div
              className='menu-mobile'
              onClick={() => {
                props.changeMobile()
              }}
            >
              <img className='hamburger'
                   src={closeMobile}
                   alt='Menu' />
            </div>
            :
            <div
              className='menu-mobile'
              onClick={() => {
                props.changeMobile()
              }}
            >
              <img className='hamburger'
                   src={menuMobile}
                   alt='Menu' />
            </div>
          }
        </div>
        <div onClick={props.changeMobile}>
          <div
            className={`navigation-items ${
              props.isMobileOpen ? 'navigation-items--open' : ''
            }`}>
            <Link activeClassName='active'
                  to={`/`}
                  replace
                  className='navigation-item'>
              Home
            </Link>
            <Link
              activeClassName='active'
              to={`/blog`}
              className='navigation-item'
              partiallyActive={true}>
              Blog
            </Link>
            <Link
            activeClassName='active'
            to={`/services`}
            className='navigation-item'
            partiallyActive={true}>
            Services
            </Link>
            <Link
              activeClassName='active'
              to={`/about`}
              className='navigation-item'
              partiallyActive={true}>
              About
            </Link>
            <Link
              activeClassName='active'
              to={`/jobs`}
              target='_blank'
              rel='noreferrer noopener'
              className='navigation-item'
              partiallyActive={true}>
              Jobs
            </Link>
            <Link
              activeClassName='active'
              to={`/contact`}
              target='_blank'
              rel='noreferrer noopener'
              className='navigation-item'
              partiallyActive={true}>
              Contact
            </Link>
          </div>
        </div>

      </header>
    </div>
  )
}
