import React from 'react'
import { Link } from 'gatsby'
import './index.scss'

export const LinkButton = ({ to, children, transparant = false }) => {
  return (
    <Link
      className={`link-btn ${transparant ? 'link-btn--transparent' : ''}`}
      to={to}
    >
      {children}
    </Link>
  )
}
